import { mapGetters } from 'vuex'
import TreeGrid from 'base/tree-grid/tree-grid.vue'
import { SearchBox, FormList, FooterBar } from 'components/index.js'
import {
  formatDate,
  removeList,
  organizeParams,
  listToTreeData,
  getTableType,
  checkChildTableIsAdded,
  setChildTableData
} from 'common/js/Util.js'
import { commonComponentMixin } from 'common/js/mixin.js'
import {
  FormLoad,
  FormSave
} from 'api/index.js'
import FormRow from 'base/form-row/form-row.vue'

export default {
  mixins: [commonComponentMixin],
  props: {
    KeyValue: {
      type: String,
      default: ''
    },
    comboboxdata: {
      type: Object,
      default () {
        return {}
      }
    },
    formAllConfig: {
      type: Object,
      default () {
        return {}
      }
    },
    chileTableItem: {
      type: Object,
      default () {
        return {}
      }
    },
    tableData: {
      type: Object,
      default () {
        return {}
      }
    },
    mainformData: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  computed: {
    ...mapGetters([
      'routerParams'
    ])
  },
  data () {
    return {
      KeyWord: '',
      formState: '',
      showCheckBox: false,
      tagShow: false,
      offset: 100,
      tableLists: [],
      defaultData: [],
      selectItems: [],
      currentItem: {},
      tableShowField: [],
      option: {
        title: {
          text: '',
          show: true
        },
        left: {
          text: '',
          show: true
        },
        center: {
          text: '',
          show: true
        },
        right: {
          text: '',
          show: true
        }
      },
      tableType: '',
      mx_isLoading: false,
      mx_message: '',
      mx_alertShow: false,
      mx_alertTitle: '提示',
      mx_toastShow: false,
      mx_toastText: '',
      mx_deleyTime: 1000
    }
  },
  mounted () {
    this.formState = this.routerParams.formstate
    // 子表初始化
    this.tableInit()
  },
  methods: {
    // 初始化
    tableInit () {
      let formListOption = this.chileTableItem.formListOption
      let textNameOption = formListOption.textNameOption
      let tableType = this.chileTableItem.tableListType

      this.tableShowField = this.chileTableItem.tableShowField
      this.option = Object.assign(this.option, textNameOption)
      this.KeyWord = this.chileTableItem.KeyWord
      this.tableType = getTableType(tableType)
    },
    formRowChange (item) {
      this.currentItem[item.field] = item.value
    },
    // 新增
    _add () {
      this.addFormLoad()
      this.offset = 0
    },
    // 保存当前数据
    saveFromData () {
      let formDate = 'auto'
      let foreignfield = this.chileTableItem.data.foreignfield
      let KeyWordType = this.chileTableItem.KeyWordType
      let isAdded = checkChildTableIsAdded(foreignfield, this.currentItem)

      if (!isAdded) {
        formDate = 'edit'
      } else {
        formDate = 'add'
        if (this.KeyValue === '0000') {
          this.MixinAlertShowEvent('需先保存主表')
          return false
        }
        this.currentItem =
          setChildTableData(foreignfield, this.currentItem, this.mainformData)
      }

      let obj = {
        KeyWord: this.KeyWord,
        formAllConfig: this.formAllConfig,
        KeyWordType: KeyWordType,
        data: [this.currentItem],
        formDate: formDate,
        FormId: this.routerParams.FromId
      }
      let params = organizeParams(obj)
      this.MinXinHttpFetch(FormSave(params), (response) => {
        this.mx_toastText = '保存成功'
        this.mx_toastShow = true
        this.$emit('saveChildFrom')
      })
    },
    // 删除当前选中的数据
    deleteSelectedItems () {
      let formDate = 'delete'
      let obj = {
        KeyWord: this.KeyWord,
        formAllConfig: this.formAllConfig,
        KeyWordType: 'BO',
        data: this.selectItems,
        formDate: formDate,
        FormId: this.routerParams.FromId
      }

      let params = organizeParams(obj)
      this.MinXinHttpFetch(FormSave(params), (response) => {
        this.mx_toastText = '删除成功'
        this.mx_toastShow = true
        this.$emit('saveChildFrom')
        this._backPre()
      })
    },
    // 初始化一条子表记录
    addFormLoad () {
      let params = {
        KeyWord: this.KeyWord,
        KeyValue: '',
        KeyWordType: 'BO',
        swhere: '',
        select: '',
        formstate: 'add'
      }
      this.MinXinHttpFetch(FormLoad(params), (response) => {
        let value = response.data.value
        this.currentItem = JSON.parse(value)
      })
    },
    // 查看一条数据
    selectItemView (item) {
      if (this.showCheckBox) {
        return false
      } else {
        this.currentItem = item
        this.offset = 0
      }
    },
    hideCheckBox () {
      this.showCheckBox = true
    },
    // 返回
    _backPre () {
      let formListUnits = this.$refs.formListUnit
      formListUnits.forEach(item => {
        item.backDefault()
      })
      this.showCheckBox = false
      this.selectItems = []
    },
    // 确定删除
    _delete () {
      this.deleteSelectedItems()
    },
    // 删除
    _preDelete () {
      this.showCheckBox = true
    },
    // 选中一条数据的事件
    _selectItem (item) {
      this.selectItems.push(item)
    },
    // 取消选中事件
    _unSelectItem (item) {
      removeList(this.selectItems, item)
    },
    // 关闭面板
    closeEditBlock () {
      this.offset = 100
    },
    _formatDate (date) {
      return formatDate(date)
    },
    formatTableLists () {
      let arr = []
      this.defaultData.forEach((item) => {
        if (!item.ParentId || item.ParentId === '00000000-0000-0000-0000-000000000000') {
          arr.push(item)
        }
      })

      arr.forEach((item) => {
        listToTreeData(item, this.defaultData)
      })

      return arr
    }
  },
  watch: {
    tableData: {
      handler (newData, oldData) {
        if (newData.values) {
          this.defaultData = newData.values.concat()
          this.tableLists = this.formatTableLists()
        }
      },
      immediate: true
    }
  },
  components: {
    SearchBox,
    FormList,
    FooterBar,
    TreeGrid,
    FormRow
  }
}



